import React, { useEffect } from "react";
import { Form, Select, Input } from "antd";
import { useNavigate } from "react-router-dom";
import { sendUserData } from "redux/slices/user/slice";
import { useDispatch } from "react-redux";
import { useAppSelector } from "redux/store";
import { Notification, AuthForm } from "components/container";
import { SignupValues } from "./SignUp.props";
import Regex from "utils/regex";

const Login: React.FC = () => {
	const navigate = useNavigate();
	const dispatch = useDispatch();
	const notification = useAppSelector((state) => state.notification);

	useEffect(() => {
		if (notification.isSuccessful) {
			const notificationProps = {
				type: "success",
				description: notification.message,
				key: "message"
			};
			Notification(notificationProps);
			navigate("/signup/check-phone");
		}
	}, [notification, navigate]);

	const onFinish = async (values: SignupValues) => {
		dispatch(sendUserData(values));
	};

	return (
		<AuthForm title=" " onFinish={onFinish} submitText=" ">
			<Form.Item
				label=" "
				name="nationality"
				rules={[{ required: true, message: "    " }]}
			>
				<Select>
					<Select.Option value="Iranian"></Select.Option>
					<Select.Option value="Foreigner"></Select.Option>
				</Select>
			</Form.Item>

			<Form.Item
				label=" "
				name="merchantCode"
				rules={[
					{ required: true, message: "    " },
					{
						pattern: Regex.nationalcode,
						message: "        ",
					},
				]}
			>
				<Input />
			</Form.Item>

			<Form.Item
				label=" "
				name="phoneNumber"
				rules={[
					{ required: true, message: "    " },
					{
						pattern: Regex.mobileNumber,
						message: "       ",
					},
				]}
			>
				<Input className="w-100" />
			</Form.Item>

			<Form.Item
				label=""
				name="email"
				rules={[
					{ required: true, message: "   " },
					{
						pattern: Regex.email,
						message: "      ",
					},
				]}
			>
				<Input type="email" />
			</Form.Item>
		</AuthForm>
	);
};

export default Login;
